<template>
  <div class="place">
    <div class="place__box">
      <div class="map__box">
        <p class="place__dec">
          <i class="iconfont icondingwei"></i> 贵阳市报名地点：贵州省贵阳市贵安数字经济产业园2号楼17层
        </p>
        <p class="tel__phone">
          <i class="iconfont icondianhua"></i> 贵阳市报名电话：0851-88574688
        </p>
        <baidu-map
          :offset="{height:-600}"
          class="map"
          @ready="handler"
          :zoom="zoom"
          :center="center"
          @click="scale"
          @dbclick="scale2"
        >
          <bm-info-window
            :position="{lng:106.633517, lat: 26.407124}"
            title="孚格教育"
            :show="infoWindow.show"
            :offset="{height:-35}"
          >
            <p v-text="infoWindow.contents"></p>
            <p v-text="infoWindow.phone"></p>
          </bm-info-window>

          <bm-marker
            :position="{lng: 106.633517, lat:26.407124}"
            :dragging="true"
            animation="BMAP_ANIMATION_BOUNCE"
            :v-show="false"
          >
            <bm-label
              content="孚格教育"
              :labelStyle="{color: 'red', fontSize : '24px'}"
              :offset="{width: -35, height: 30}"
            />
          </bm-marker>
        </baidu-map>

        <CollectionUser :isGuiYang="true" />
      </div>

      <NeedKnow />
    </div>
  </div>
</template>

<script>
import CollectionUser from "../components/CollectionUser";
import NeedKnow from "../components/NeedKnow";

export default {
  name: "Place",
  data() {
    return {
      height: 50,
      center: { lng: 0, lat: 0 },
      zoom: 0,
      zoomshow_arr: [7, 15, 19],
      zoom_index: 0,
      infoWindow: {
        show: true,
        contents: "地址：贵州省贵阳市贵安数字经济产业园2号楼17层",
        phone: "电话：0850-88574688"
      }
    };
  },
  components: {
    CollectionUser,
    NeedKnow
  },
  methods: {
    handler({ BMap, map }) {
      console.log(BMap, map);
      this.center.lng = 106.633517;
      this.center.lat = 26.407124;
      this.zoom = this.zoomshow_arr[this.zoom_index];
    },

    scale(e) {
      ++this.zoom_index;

      if (this.zoom_index >= this.zoomshow_arr.length) {
        this.zoom_index = 0;
      }
      this.zoom = this.zoomshow_arr[this.zoom_index];
      this.center.lng = 106.633517;
      this.center.lat = 26.407124;
      this.infoWindow.show = true;
    },

    scale2(e) {
      this.zoom = 19;
      this.center.lng = 106.633517;
      this.center.lat = 26.407124;
    }
  }
};
</script>


<style lang="scss" scoped>
@mixin blockCenter {
  width: 1200px;
  margin: 0 auto;
  height: 100%;
  box-sizing: border-box;
}
@mixin li_row_center {
  display: flex;
  align-items: center;
}
@mixin oneRow($width) {
  width: $width;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
@mixin ellipse($width) {
  width: $width;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

// .map {
//   width: 400px;
//   height: 300px;
// }

div.place__box {
  @include blockCenter;
  height: auto;
  box-sizing: border-box;
  padding: 37px 16px 25px 33px;
  border: 1px solid rgba(217, 217, 217, 1);
  margin-bottom: 24px;
  display: flex;

  div.map__box {
    width: 606px;
    margin-right: 65px;
    box-sizing: border-box;
    & > p {
      &.place__dec {
        margin-top: 16px;
        margin-bottom: 6px;
      }
      font-size: 17px;
      font-weight: 400;
      color: rgba(0, 0, 0, 1);
      line-height: 30px;
      vertical-align: middle;
      @include ellipse(100%);
      @include li_row_center;
      i {
        font-size: 24px;
        color: #d9d9d9;
        margin-right: 16px;
      }
    }

    .map {
      width: 606px;
      height: 300px;
      margin-top: 13px;

      div.float__info {
        width: 300px;
        height: 150px;
        background: pink;
      }
    }
  }
}
</style>

<style lang="scss">
.map {
  .anchorBL {
    display: none !important;
  }
  .BMap_cpyCtrl.BMap_noprint.anchorBL {
    display: none !important;
  }
  .BMap_bubble_content {
    p {
      font-size: 12px;
      line-height: 1.3;
      white-space: nowrap;
    }
  }
  .BMap_bubble_title {
    color: cornflowerblue;
    font-size: 12px;
    line-height: 1.3;
  }
  .BMapLabel {
    font-size: 12px !important;

    top: 36px !important ;
    left: -18px !important;
  }
}
</style>